.help-detail {
    // margin-left: calc(-2 * var(--padding-x));
    // margin-right: calc(-2 * var(--padding-x));
    
    .detail-wrap {
        background-color: #FFFFFF;
        border-radius: 20px;
        padding-left: calc(2 * var(--padding-x));
        padding-right: calc(2 * var(--padding-x));
        padding-bottom: 16px;
    }

    .detail-content {
        padding-top: 68px;
        padding-bottom: 76px;
        h2 {
            font-family: Gilroy-SemiBold;
            font-size: 24px;
            line-height: 20px;
            letter-spacing: -0.25px;
            text-align: left;
            color: #17274E;
            margin-bottom: 16px;

        }

        p {
            font-family: Gilroy-Regular;
            font-size: 16px;
            line-height: 24px;
            letter-spacing: 0px;
            text-align: left;
            color: #17274E;
            margin-bottom: 0;

        }
    }
}